export default {
	data() {
		return {
			buyOrSell:'',
			list:[]
			
		};
	},
	
	filters:{
		returnStyle:function(value){
			switch(value){
					case 'SALES_DIRECTOR':
					   return '销总'
					   break;
					case  'REGIONAL_MANAGER':
					  return '区经'
					  break;
					case 'BUY_DEPUTY':
					  
					  return '买办'
					  break;
					case 'SELL_DEPUTY':
					  return '卖办'
					  break;
					case 'ANALYST':
					  return '分析'
					  break;
			   }
		}
	},
	onLoad:function(options){
		this.buyOrSell = options.buyOrSell
		this.fetch()
	},
	methods:{
		
		async fetch(){
			const res = await this.$http.get('/latent/at_list',{
				data:{buyOrSell: this.buyOrSell}
			})
			console.log(res)
			this.list = res.data.data.list
		},
		selectContact:function(realName){
			
			let pages = getCurrentPages();
			// var currPage = pages[pages.length - 1]; //当前页面
			let prevPage = pages[pages.length - 2]; //上一个页面
			
			prevPage.$vm.content = prevPage.$vm.content + `${realName} `
			
			uni.navigateBack({
				delta:  -1
			});
		}
		
	}
}
